@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800&display=swap');
@import url("https://fonts.googleapis.com/css?family=Sofia&effect=neon|outline|emboss|shadow-multiple");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: radial-gradient(circle at top right,rgba(23,33,94,.9) 0,#f2b04f 25%); */
  background: linear-gradient(45deg, rgb(201, 17, 198), rgb(203, 220, 226));
}


element.style {
  font-weight: bold;
  font-size: 60px;
  color: rgb(250, 246, 250);
  margin-top: 20px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: monospace;
}
.font-effect-neon {
  text-shadow: 0 0 0.1em #f0f0f0, 0 0 0.2em #f1ebeb, 0 0 0.3em #fff, 0 0 0.4em rgb(248, 245, 248), 0 0 0.6em rgb(239, 230, 239), 0 0 0.8em rgb(244, 237, 244), 0 0 1em rgb(238, 232, 238), 0 0 1.2em rgb(245, 235, 245);
  color: rgb(8, 255, 74);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.mybutton {
  color: rgb(250, 248, 248) !important;
  background-color: #57a4e4 !important;
  box-shadow: none !important;
}

.mybutton:hover {
  color:rgb(119, 6, 122) !important;
  background-color: #52a1e2 !important;
}

@media (max-width: 765px) {
  .mybutton {
    color: white !important;
    background-color: #43a5f3 !important;
    box-shadow: none !important;
  }
}

.wrapper {
  border-radius: 0px !important;
  border: 2px solid #b92be9;
  box-shadow: none !important;
}
